<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
		<input-driver-chip :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'inputDriverChip')" :nextFunction="getDriverChip"/>
		<process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <div v-if="currentStep === steps.findIndex(step=>step.key === 'complete')">
            <div class="kd-page-title">
                驾驶人基本信息查询结果
            </div>
            <div class="kd-page-content-wrapper content">
                <text-table :data="driverUserInfo"/>
            </div>
            <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
                <kd-button
                    icon="kedaIcon-policework"
                    text="业务完成"
                    typeClass="complete"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import textTable from '@/components/text-table';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import { driverLicenseQueryUserInfos } from '@/service/traffic/traffic';
import { translateLicense } from '@/utils/tools';
import inputDriverChip from '@/view/pageComponents/input-driver-chip';
import { LOCATION_ID } from '@/constant/constant';
import { DRIVER_LICENSE_CHECK, DRIVER_LICENSE_CHECK_YINCHUAN } from '@/constant/stepsOption';

export default {
    name: 'DriverInfoQueryIndex',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        textTable,
        process,
        inputDriverChip
    },
    computed: mapState('common', {
        steps: 'steps',
        currentStep: 'currentStep',
        identifyCard: 'identifyCard'
    }),
    data () {
        return {
            driverUserInfo: []
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        getDriverChip (chip) {
            this.chip = chip;
            this.changeStep('down');
        },
        async handleBusiness () {
            const res = await driverLicenseQueryUserInfos({
                idNum: this.identifyCard.certNumber,
                zxbh: this.chip
            });
            if (res && res.code === '0') {
                this.driverUserInfo = translateLicense(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    imgName: 'imgDriverPerson'
                });
            }
        }
    },
    mounted () {
        if (this.CONSTANT.location === LOCATION_ID.yinchuan || this.CONSTANT.location === LOCATION_ID.zhongwei) {
            this.setSteps(DRIVER_LICENSE_CHECK_YINCHUAN);
        } else {
            this.setSteps(DRIVER_LICENSE_CHECK);
        }
    }
};

</script>
<style lang="scss" scoped>
    .content {
        margin: 1vh auto 0;
    }
</style>
